@charset "UTF-8";
/* For all devices */
.field {
  padding: 0 2px;
}
.field:not(:first-child) {
  margin-top: 20px;
}
.field:not(:last-child) {
  margin-bottom: 20px;
}

.field__label {
  display: inline-block;
  font-weight: 700;
}
.field__label span {
  font-weight: 400;
  color: #7b8676;
}

.field__feedback,
.field__error {
  display: none;
  width: 100%;
  margin: 0;
  font-weight: 700;
}

.field__feedback {
  color: #00a000;
}

.field__error {
  color: #f74440;
}

.input {
  width: 100%;
  margin: 5px auto;
  padding: 6px 12px;
  border: solid #7b8676 1px;
  border-radius: 2px;
  background: #ffffff;
  color: #333333;
  font: inherit;
  outline: none;
  transition: all 0.3s ease-in-out;
}
.input:disabled {
  opacity: 0.65;
}
.field .input {
  margin-bottom: 0;
}

.input::placeholder {
  color: #cdcdcd;
}

.input:focus {
  box-shadow: 0 0 8px #0078c9;
  border-color: #0078c9;
}

.input--invalid {
  border-color: #f74440;
}

.input--invalid:focus {
  border-color: #f74440;
  box-shadow: 0 0 8px rgba(247, 68, 64, 0.6);
}

.input--valid {
  border-color: #00a000;
}

.input--valid:focus {
  border-color: #00a000;
  box-shadow: 0 0 8px rgba(0, 160, 0, 0.6);
}

/* Only for Desktops */
@media only screen and (min-width: 1200px) {
  .input {
    padding: 9px 14px;
  }
}
.projects__header {
  margin: 16px auto;
}

.projects__type-filter,
.projects__pagination-status {
  display: block;
  margin: 10px 0;
}

.projects__type-filter-label {
  margin: 0;
  font-weight: 700;
  vertical-align: middle;
}

.projects__type-filter-select {
  width: auto;
  margin: 0;
  padding: 0 4px 0 0;
  background: transparent;
  border-color: transparent;
  vertical-align: middle;
}
.projects__type-filter-select:focus {
  background: #fff;
  border-color: #7b8676;
}

.projects__loading {
  width: 100%;
  margin: 25px auto;
}

.projects__error {
  display: none;
  width: 100%;
  margin: 25px auto 0;
  font-weight: 700;
  color: #f74440;
}

.projects__items {
  margin: 25px auto;
}

.project {
  margin-top: 24px;
  padding: 0 15px 15px;
  border: 1px solid #eeeeee;
  border-radius: 10px;
  background-color: #ffffff;
  box-shadow: 0 0 10px 0 rgba(51, 51, 51, 0.2);
}

.project:first-child {
  margin-top: 0;
}

.project__header,
.project__meta {
  display: flex;
  align-items: center;
}

.project__header {
  margin: 8px 0 0;
}

.project__meta {
  margin: 0 0 15px;
}

.project__image {
  position: relative;
  overflow: hidden;
  min-height: 300px;
}
.project__image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 100%;
  width: auto;
}

.project__title {
  width: 100%;
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-size: 22px;
  line-height: 24px;
  font-weight: 700;
  text-align: left;
}

.project__date {
  width: 100%;
  margin: 0 0 0 auto;
  font-size: 14px;
  text-align: right;
  color: #737373;
}

.project__type {
  width: 100%;
  margin: 15px 0 0;
  font-size: 14px;
  font-weight: 700;
  text-align: left;
}

.project__tags {
  margin: 15px 0;
}

.project__tag {
  display: inline-block;
  margin: 2px;
  padding: 5px;
  color: #0078c9;
}

.project__description {
  text-align: left;
  margin-top: 15px;
}
.project__description ol, .project__description ul {
  list-style: inside;
  margin: 10px 0;
  padding-left: 20px;
}
.project__description li {
  margin: 2px 0;
}

.project__description > p:first-child {
  margin-top: 0;
}

.project__description > p:last-child {
  margin-bottom: 0;
}

.project__footer {
  display: flex;
  margin: 15px 0 0;
  justify-content: space-between;
}

.project__link {
  display: inline-block;
  position: relative;
  margin: 0 12px;
  color: #0078c9;
}

.project__link:first-child {
  margin-left: 0;
}

.project__link:last-child {
  margin-right: 0;
}

.project__link:hover,
.project__link:focus {
  color: #0081D6;
}

.project__link::after {
  content: "Â ";
  position: absolute;
  top: 25%;
  height: 50%;
  width: 2px;
  margin: 0 12px;
  background: rgba(123, 134, 118, 0.2);
}

.project__link:last-child::after {
  content: none;
}

.project__read-more {
  position: relative;
  margin: 0 36px 0 auto;
  padding: 5px 5px 5px 12px;
  border: 0;
  border-radius: 30px 0 0 30px;
  font-weight: 700;
}

.project__read-more::after {
  content: "\f35a";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 100%;
  background: #0078c9;
  font-family: "Font Awesome 5 Free";
  font-size: 36px;
  line-height: 32px;
  font-weight: 900;
  color: #ffffff;
  text-align: center;
  border-radius: 0 30px 30px 0;
  transition: background-color 0.2s ease-in-out;
}

.project__read-more:hover::after,
.project__read-more:focus::after {
  background-color: #0081D6;
}

.detailed-project .project__links {
  margin: 15px 0;
}

.detailed-project .project__type {
  margin: 0;
}

@media only screen and (min-width: 768px) {
  .projects__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .projects__types-filter {
    margin: 40px auto;
  }
  .projects__type-filter,
  .projects__pagination-status {
    margin: 0;
  }
  .projects__items {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 40px auto;
  }
  .project {
    display: flex;
    width: 100%;
    padding: 0;
    align-items: center;
  }
  .project:nth-child(even) {
    flex-direction: row-reverse;
  }
  .project__image {
    margin: 0;
    width: 50%;
  }
  .project__content {
    padding: 10px 20px;
    width: 50%;
  }
  .project__type {
    margin-top: 0;
  }
}
@media only screen and (min-width: 1200px) {
  .projects .container > * {
    width: 80%;
    margin-right: auto;
    margin-left: auto;
  }
  .projects__header {
    margin: 40px auto;
  }
  .projects__pagination {
    margin-bottom: 10px;
  }
  .project__read-more::after {
    font-size: 40px;
    line-height: 33px;
  }
}
.pagination__item {
  display: inline-block;
  margin: 2px 8px;
}

.pagination__link {
  display: inline-block;
  min-height: 38px;
  min-width: 38px;
  padding: 8px 14px;
  border-radius: 5px;
  background: #ffffff;
  color: #333333;
  transition: all 0.25s ease-in-out;
}
.pagination__link:hover, .pagination__link:focus {
  background: #ffeb05;
  box-shadow: 0 0 10px 0 rgba(51, 51, 51, 0.2);
}

.pagination__link--active,
.pagination__link--active:hover,
.pagination__link--active:focus {
  background: #ffeb05;
  cursor: default;
  box-shadow: none;
}

.slide-show {
  margin: 0 -15px;
}

.slide-show__viewport {
  position: relative;
  overflow: hidden;
  transition: all 1s ease-in-out;
}

.slide-show__viewport:hover {
  cursor: pointer;
  box-shadow: 0 0 10px 0 rgba(51, 51, 51, 0.3);
}

.slide-show__slides {
  display: flex;
  position: relative;
  width: 100%;
  left: 0;
  align-items: center;
  transition: left 2s ease;
  z-index: 1;
}

.slide-show__slide {
  position: relative;
  margin: auto;
}

.slide-show__image {
  max-width: 100%;
  max-height: 350px;
  transition: all 1s ease-in-out;
}

.slide-show__image:hover {
  transform: scale(1.1);
}

.slide-show__nav {
  position: absolute;
  top: calc(50% - 15px);
  padding: 0;
  z-index: 2;
}

.slide-show__nav[data-direction=previous] {
  left: 0;
}

.slide-show__nav[data-direction=next] {
  right: 0;
}

.slide-show__nav svg {
  width: 30px;
  height: 30px;
  opacity: 0.8;
  background: #ffffff;
  box-shadow: 0 0 20px 0 rgba(51, 51, 51, 0.3);
  transition: all 0.3s ease-in-out;
}

.slide-show__nav[data-direction=previous] svg {
  border-radius: 0 50% 50% 0;
}

.slide-show__nav[data-direction=next] svg {
  border-radius: 50% 0 0 50%;
}

.slide-show__nav:hover svg,
.slide-show__nav:focus svg {
  opacity: 1;
  cursor: pointer;
  box-shadow: 0 0 20px 0 #333333;
}

.slide-show__nav svg path {
  fill: #333333;
  transition: all 0.3s ease-in-out;
}

.slide-show__bullets {
  margin: 15px auto;
}

.slide-show__bullet {
  display: inline-block;
  width: 12px;
  height: 12px;
  margin: auto 8px;
  border-radius: 50%;
  background-color: #e1e1e1;
  vertical-align: middle;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
}

.slide-show__bullet--active {
  background-color: #333333;
  transform: scale(1.2);
}

.slide-show__bullet:not(.slide-show__bullet--active):hover,
.slide-show__bullet:not(.slide-show__bullet--active):focus {
  background-color: #c8c8c8;
  transform: scale(1.05);
}

.expanded-slide-show {
  top: 50%;
  left: 50%;
  width: 1px;
  height: 1px;
  background: transparent;
  opacity: 0;
  transition: all 1s ease-in-out;
  transition-property: background, top, left, height, width, opacity;
  cursor: initial;
}

.expanded-slide-show--open {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 100;
  background: #f5f5f5;
}

.expanded-slide-show--closing {
  z-index: 100;
}

.expanded-slide-show__image-container {
  display: flex;
  position: absolute;
  top: 20px;
  left: 10px;
  width: calc(100% - 20px);
  height: calc(100% - 55px);
  align-items: center;
}

.expanded-slide-show__image {
  max-width: 100%;
  max-height: 100%;
  margin: auto;
  box-shadow: 0 0 10px 0 rgba(51, 51, 51, 0.3);
  opacity: 0;
  transition: all 1s ease-in-out;
}

.expanded-slide-show__image--active {
  opacity: 1;
}

.expanded-slide-show__controls {
  display: flex;
  position: absolute;
  bottom: 0;
  width: 100%;
  align-items: center;
  padding: 0 15px;
  background-color: #000000;
  color: #ffffff;
}

.expanded-slide-show__bullets {
  width: 33%;
}

.expanded-slide-show__bullet {
  display: inline-block;
  width: 12px;
  height: 12px;
  margin: 5px;
  border-radius: 50%;
  background-color: #e1e1e1;
  vertical-align: middle;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
}

.expanded-slide-show__bullet--active {
  background: #ffffff;
  transform: scale(1.2);
}

.expanded-slide-show__bullet:not(.expanded-slide-show__bullet--active):hover,
.expanded-slide-show__bullet:not(.expanded-slide-show__bullet--active):focus {
  background-color: #c8c8c8;
  transform: scale(1.05);
}

.expanded-slide-show__navigations {
  width: 33%;
  text-align: left;
}

.expanded-slide-show__nav {
  opacity: 0.8;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.expanded-slide-show__nav:hover,
.expanded-slide-show__nav:focus {
  opacity: 1;
}

.expanded-slide-show__nav svg {
  width: 28px;
  height: 28px;
}

.expanded-slide-show__counter {
  width: 33%;
  margin: 0;
  text-align: right;
}

.expanded-slide-show__counter span {
  font-weight: 700;
}

.expanded-slide-show__close {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  padding: 7px 10px;
  min-width: 32px;
  min-height: 32px;
  font-size: 21px;
  z-index: 1;
  background: #f12525;
}
.expanded-slide-show__close:hover, .expanded-slide-show__close:focus {
  background-color: #ff2525;
}

.portfolio-links {
  padding: 10px;
}

.portfolio-links__heading.portfolio-links__heading {
  margin: 10px 0 0;
  color: #0078c9;
}

.portfolio-links__image {
  height: 50px;
  margin: 15px 3px;
  background: #0078c9;
}

@media only screen and (min-width: 768px) {
  .portfolio-links__heading.portfolio-links__heading {
    margin-top: 15px;
  }
  .portfolio-links__image {
    height: 60px;
    margin: 20px 10px;
  }
}
@media only screen and (min-width: 1200px) {
  .portfolio-links__image {
    height: 70px;
    margin: 25px 16px;
  }
}